import React, { Component } from 'react';
import axios from 'axios';
import {Modal, Button} from 'reactstrap';
import PageList from './components/PageList'
// import TempImage from "../../../TempImage";
// import Products_1_ImageLeft from '../../components/molecules/Products/ProductsOneImageLeft'
// import Products_1_ImageRight from '../../components/molecules/Products/ProductsOneImageRight'
// import Products_2 from '../../components/molecules/Products/Products_2'
// import Products_3_ImageLeft from '../../components/molecules/Products/Products_3_ImageLeft'
// import Products_3_ImageRight from '../../components/molecules/Products/Products_3_ImageRight'
// import Products_4 from '../../components/molecules/Products/Products_4';

import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
 


export class PageBuilder extends Component{
    constructor (props) {
        super(props)
        this.state = {
            htmlEL: '',
            toggle: false,
            toggle2: false,
            pageLog:[],
            view: 'pc',
            menuView: 'Select'

        }
        this.container = React.createRef()
        this.onItemRemove = this.onItemRemove.bind(this);
        this.getOptions = this.getOptions.bind(this);
        this.menuView = this.menuView.bind(this)

    }

    getHtml = () => {
        navigator.clipboard.writeText(this.container.current.innerHTML)
            this.setState({
                htmlEL: this.container.current.innerHTML
            })
        this.toggle()
       
    }
    toggle = () => {
        this.setState({
            toggle: !this.state.toggle
        })
        
    }
    toggle2 = () => {
        this.setState({
            toggle2: !this.state.toggle2
        })
    }
    pageType =(el) => {
        console.log(el)
        let random = Math.floor(Math.random() * Math.floor(100));
        this.setState ( {
            pageLog:  [ ...this.state.pageLog, 
                {
                    el: el,
                    id: el + '-' + random
                }
            ]
        }, ()=> {
            if(this.container){
                this.container.current.scrollIntoView({behavior: 'smooth', block: 'end'})
            }
        })
    }
    onItemRemove = (i) => {
        let log = this.state.pageLog;
        if(i === 0){
            log.shift()
        }else{
            log.splice(i, 1);
        }
        this.setState({
            pageLog: log,
        })
    }
    onItemEdit = (i) => {
        this.setState({
            editItem: i,
        })
    }
    getOptions = (i, options) => {
        let pageLog = [...this.state.pageLog];
        pageLog[i] = {
            ...pageLog[i],
            options: options
        }
        this.setState({
            pageLog
        })
        console.log('set options')
    }

    updateOptions = (type, data, i) =>{
        let pageLog = [...this.state.pageLog];
        pageLog[i] = {
            ...pageLog[i],
            options: {
            ...pageLog[i].options,
            [type]: data
            }
        }

        if(pageLog[i].options.items){
            let items = [];
            for(let count = 0; count<4; count++){
                items.push(pageLog[i].options.types[pageLog[i].options.type].item)
            }
            pageLog[i] = {
                ...pageLog[i],
                options: {
                ...pageLog[i].options,
                items: items
                }
            }
        }
        this.setState({
            pageLog
        })
        console.log('update options')

    }

    menuView = (view) => {
        this.setState({
            menuView: view
        })
    }


    authZeplin = () => {
        var options = {
            method: 'GET',
            url: 'https://api.zeplin.dev/v1/oauth/authorize',
            qs: {
              response_type: 'code',
              client_id: '5f0b36015341df7e1af78346',
              redirect_uri: 'https://tiffanymarroq.github.io',
            }
          };
        window.location = `https://api.zeplin.dev/v1/oauth/authorize?client_id=${options.qs.client_id}&redirect_uri=${options.qs.redirect_uri}&response_type=code`
        
    }
    getZeplin = () =>{
        var options = {
            method: 'GET',
            url: 'https://api.zeplin.dev/v1/oauth/token',
            headers: {accept: 'application/json'}
          };
        console.log('post')
        let query = window.location.search.split('=');
        let q = query[1];
        axios.post(options.url,
            {
                grant_type: 'authorization_code',
                code: q,
                redirect_uri: 'https://tiffanymarroq.github.io',
                client_id: '5f0b36015341df7e1af78346',
                client_secret: '9d64785a16e02b78e40ab63373a17711b549337e1284df5d2eaa1d6de1801b18'
            }
        )
        .then((res)=>console.log(res))
        .catch((err)=>console.log(err))
    }
    getScreens = () =>{
        console.log('get screen')
        axios.get(`https://api.zeplin.dev/v1/projects/5e0785acd852e81aeecd8eef/screens/5f0b3568daf4947e91bd2df1/versions/latest`)
        .then(res=>console.log(res))
        .catch(err=>console.log(err))
    }

    reorder = (list, startIndex, endIndex) => {
        const result = Array.from(list);
        const [removed] = result.splice(startIndex, 1);
        result.splice(endIndex, 0, removed);
      
        return result;
      };
    onDragEnd = (result) => {
        // dropped outside the list
        if (!result.destination) {
          return;
        }
        console.log(this.state, 'reorder log')
    
        const pageLog = this.reorder(
          this.state.pageLog,
          result.source.index,
          result.destination.index
        );
    
        this.setState({
            pageLog
        });
      }

       getItemStyle = (isDragging, draggableStyle) => ({
        // some basic styles to make the items look a bit nicer
        userSelect: "none",
        padding: 8 * 2,
        margin: `0 0 ${8}px 0`,
      
        // change background colour if dragging
        background: isDragging ? "lightgreen" : "grey",
      
        // styles we need to apply on draggables
        ...draggableStyle
      });
      
       getListStyle = isDraggingOver => ({
        background: isDraggingOver ? "lightblue" : "lightgrey",
        padding: 8,
        width: 200
      });
      
    render(){
        let {view,pageLog} = this.state;
        console.log(pageLog, 'loggggg')
        return (
            <div style={{backgroundColor: '#fff',paddingTop: 80, paddingBottom: 50, maxWidth: 1280, margin: '0 auto', minHeight: '100vh'}}>
             
                <div ref={this.container} className={'page page-' + view}>
                    <PageList pageLog={this.state.pageLog} onItemRemove={this.onItemRemove} getOptions={this.getOptions} updateOptions={this.updateOptions}/>
                </div>
                <div>
                    <div style={{width: 'calc(100% - 80px)',position:'fixed', top: 20, left: 50, display: 'flex', justifyContent: 'space-between'}}>
                        <button style={{padding: '10px 25px', border: 0, borderRadius: '4px', backgroundColor: '#1b1b1b', color: 'white', fontSize: '16px', fontWeight: 'bold'}} onClick={this.toggle2}>Menu</button>
                        <button style={{padding: '10px 25px', border: 0, borderRadius: '4px', backgroundColor: '#1b1b1b', color: 'white', fontSize: '16px', fontWeight: 'bold'}} onClick={this.getHtml}>Get Code</button>
                    </div>
                    
                    <div className={'custom-slide slide-' + (this.state.toggle2 ? 'show' : '')}>
                        <div style={{display: 'flex'}}>
                            <Button style={{margin: 10}}onClick={()=>this.menuView('Select')}>Select </Button> 
                            <Button style={{margin: 10}} onClick={()=>this.menuView('Outline')}>Outline</Button>
                        </div>
                        {this.state.menuView === 'Select' ? 
                         <ul style={{padding: '20px'}}>
                            <p style={{fontWeight: 'bold'}}onClick={this.toggle2}>CLOSE</p>
                            <br />
                            <li onClick={() => this.pageType('Hero')}>Hero</li>
                            <li onClick={() => this.pageType('MenuContainer')}>Menu</li>
                            <li onClick={() => this.pageType('Video')}>Video</li>
                            <li onClick={() => this.pageType('TitleSubheading')}>TitleSubheading</li>
                            <li onClick={() => this.pageType('AboutDescription')}>About</li>
                        </ul>
                        : 
                        <DragDropContext onDragEnd={this.onDragEnd} className="scroll">
                            <p style={{fontWeight: 'bold'}}onClick={this.toggle2}>CLOSE</p>

                        <Droppable droppableId="droppable">
                        {(provided, snapshot) => (
                            <div
                            {...provided.droppableProps}
                            ref={provided.innerRef}
                            style={this.getListStyle(snapshot.isDraggingOver)}

                            >
                            {this.state.pageLog.map((log, i) => {
                               return (<Draggable key={log.id} draggableId={log.id} index={i}>
                                {(provided, snapshot) => (
                                    <li
                                        ref={provided.innerRef}
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}
                                        style={this.getItemStyle(
                                            snapshot.isDragging,
                                            provided.draggableProps.style
                                          )}
                                    >
                                    {log.el}
                                    <p>Select Type</p>
                                        <div style={{display: 'flex', padding:'10px 0', justifyContent: 'space-between'}}>
                                            {log.options && log.options.types?  Object.keys(log.options.types).map((type, j) =>{
                                                return <p  
                                                    style={{width: 20,padding: 5, border: '1px solid #777'}}
                                                    key={j} 
                                                    onClick={()=>this.updateOptions('type', type, i)}>{type}</p>
                                            }) : null}
                                        </div>
                                        <div style={{display: 'flex', justifyContent: 'space-between', position: 'relative', zIndex: 1}}>
                                            <p onClick={()=> this.onItemRemove(i)} >Delete</p>
                                            <p onClick={()=> this.onItemEdit(i)} >Edit</p>
                                        </div>
                                    </li>
                                )}
                                </Draggable>)
                            })}
                            {provided.placeholder}
                            </div>
                        )}
                        </Droppable>
                    </DragDropContext>
                   
                        }


               
                    </div>
                    <Modal toggle={this.toggle} isOpen={this.state.toggle} style={{maxWidth: '800px', padding: '20px'}}>
                        <h1>This is what gets copied</h1>
                        <br/>
                        <div style={{padding: '10px'}}>
                            {this.state.htmlEL}
                        </div>
                    </Modal>
                </div>
            </div>
        )
    }
}

