import React from 'react';
import * as UI from '../../../components/all';
import {Button} from 'reactstrap';

const PageList = (props) =>{
    console.log(props, 'page list')
    return(
        <div className="feature-page">
            {props.pageLog.map((item,i) => {
               let ItemType = UI[item.el]
               return (
               <div style={{position: 'relative'}} key={i}>
                    {item.options ?
                        <React.Fragment>
                            <ItemType getOptions={props.getOptions} data={item} options={item.options} i={i} key={i}/>
                            {/* <Button onClick={() => props.updateOptions(i)} style={{position: 'absolute', top: 20, right: 115}} color="success">Edit</Button> */}
                        </React.Fragment>
                        :
                        <ItemType getOptions={props.getOptions} data={item}  i={i} key={i}/>
                    }
                    {/* <Button onClick={() => props.onItemRemove(i)} style={{position: 'absolute', top: 20, right: 20}} color="danger">Remove</Button> */}
               </div>)
            })} 
        </div>
    )
}

export default PageList;