import React from 'react';
import {defaultValues} from '../../constants';


const defaultProps = {
    title: defaultValues.HERO_TITLE,
    show: false,
    src: defaultValues.HERO_IMAGE_PC 
};

const Hero = (props) => {
    let {
        className,
        style,
        title,
        show,
        src,
        i,
        data
    } = props

    let options ={
        title: '',
        src:'',
        show: false
    }
    console.log(props.data.options,'props')
    if(data.options === undefined ){
        props.getOptions(i, options)
    }
    return(
        <div className={'hero ' + (className || '') } style={style} >
            <img src={src} alt={title + ' Hero'}/>
            {show ? <h1 className="Header-2">{title}</h1> : ''}
        </div>
    )
}

Hero.defaultProps = defaultProps;

export default Hero;